.root {
  width: 100vw;
  height: 100vh;
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
}

.tools {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  display: flex;

  button {
    border: 1px solid rgba(#fff, .7);
    border-radius: 5px;
    background: rgba(#000, .1);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    margin: 10px 5px;
    opacity: .7;
    color: #fff;
    font-size: 24px;
    transition: .1s ease;

    &:hover {
      opacity: 1;
      cursor: pointer;
      background: rgba(#fff, .4);

    }
  }
}
