.root {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.grid {
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 0;
  z-index: 1;
}

.gridBackground {
  width: 100%;
  height: 100%;
  display: grid;
  grid-gap: 0;

  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;

  span {
    display: block;
    border: 1px solid rgba(#fff, .1);
    z-index: 0;
  }
}
