.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 10;
  position: relative;
}

.wrapper {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 0;
  bottom: 40px;
  transform: translate(50%, 50%);
}

.wrapper {
  position: absolute;
  right: 0;
  bottom: 0;
  transform: translate(50%, 50%);

  span {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -5px;
    opacity: 0;
    color: #FFF;
    pointer-events: none;
    font-size: 11px;
    transition: .1s ease;

    &:last-of-type {
      bottom: -20px;
    }
  }

  &:hover {
    cursor: pointer;
    span {
      opacity: .5;
    }
  }
}

.star {
  background: url('../../assets/svg/star.svg'), no-repeat;
  background-size: cover;
  width: 60px;
  height: 60px;
  border: none;
  outline: unset;
  transition: .1s ease;

  &:hover, &.hovered {
    cursor: pointer;
    width: 60px;
    height: 60px;
    filter: brightness(1.5);
    background: url('../../assets/svg/star_hover.svg'), no-repeat;
    background-size: cover;
  }


}
